body{
background-color: #313435;
color: white;
/* font-family: "Amatic SC", sans-serif;
font-size: 25px;
font-weight: bolder; */
}



.background-video {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -1;
    opacity: 0.3;
  }



  .container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
     text-align: center;
     
}



  .main-container {
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 4px #0000001a;
    display: flex;
    flex-direction:column;
    justify-content: center;
    width: 40%;
    height: 80%;
  
}

@media only screen and (max-width: 600px) {

    .main-container{
        width: 90%;
        height: 80%;

    }  
}





.main-container input,select{
    border: none;
    border-bottom: 1px solid #c7bebe;
    font: inherit;
    /* font-size: 15px; */
    margin: 20px;
    max-width: 100%;
    outline: none;
    padding: 0 0 5px;
    text-align: center;
    width: 60%;
    background: transparent;
    color: white;

}



.main-container input:focus{
    border-bottom: 1px solid white;
}
.main-container select:focus{
    border-bottom: 1px solid white;
}

select option{
    color: black;
    text-align: center;
}

select {
    text-align: center;
}


















.custum-file-upload {
    /* height: 200px;
    width: 300px; */
    display: flex;
    flex-direction: column;
    align-items: space-between;
    gap: 20px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    border: 2px dashed #cacaca;
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0px 48px 35px -48px rgba(0,0,0,0.1);
 
    
  }
  
  .custum-file-upload .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  
  .custum-file-upload .icon svg {
    height: 40px;
    fill: white;
    color: white;
  }
  
  .custum-file-upload .text {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .custum-file-upload .text span {
    font-weight: 400;
    color: white;
  }
  
  .custum-file-upload input {
    display: none;
    color: white;
  }